<template>
    <div>
 
      <div v-if="loading" class="text-center py-8">
            <div role="status" class="space-y-8 animate-pulse md:space-y-0 md:space-x-8 md:flex md:items-center">
                <div class="flex justify-center items-center w-full h-48 bg-gray-300 rounded sm:w-96 dark:bg-gray-700">
                    <svg class="w-12 h-12 text-gray-200" xmlns="http://www.w3.org/2000/svg" aria-hidden="true" fill="currentColor" viewBox="0 0 640 512"><path d="M480 80C480 35.82 515.8 0 560 0C604.2 0 640 35.82 640 80C640 124.2 604.2 160 560 160C515.8 160 480 124.2 480 80zM0 456.1C0 445.6 2.964 435.3 8.551 426.4L225.3 81.01C231.9 70.42 243.5 64 256 64C268.5 64 280.1 70.42 286.8 81.01L412.7 281.7L460.9 202.7C464.1 196.1 472.2 192 480 192C487.8 192 495 196.1 499.1 202.7L631.1 419.1C636.9 428.6 640 439.7 640 450.9C640 484.6 612.6 512 578.9 512H55.91C25.03 512 .0006 486.1 .0006 456.1L0 456.1z"/></svg>
                </div>
                <div class="w-full">
                    <div class="h-2.5 bg-gray-200 rounded-full dark:bg-gray-700 w-48 mb-4"></div>
                    <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[480px] mb-2.5"></div>
                    <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 mb-2.5"></div>
                    <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[440px] mb-2.5"></div>
                    <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[460px] mb-2.5"></div>
                    <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[360px]"></div>
                </div>
                <span class="sr-only">Loading...</span>
            </div>

            </div>

        <!--INFO BOXES-->


      <!--TABLE-->
      <table v-else>
        <CardBoxModal v-model="isModalActive" title="Information">
          <p>Lorem ipsum dolor sit amet <b>adipiscing elit</b></p>
          <p>This is sample modal</p>
        </CardBoxModal>
          <!--END INFO BOXES-->
          <thead class="text-xs text-gray-700 uppercase dark:text-gray-400">
          <tr>
            <th />
            <th>Name</th>
            <th>Marketcap</th>
            <th />
          </tr>
        </thead> 
        <tbody>
          <tr v-for="asset in assetsPaginated" :key="asset.name">
            <th data-label="logo" class="h-6 w-6">
              <div class="h-6 w-6">                        
                <img :src="asset.logo ? asset.logo : 'https://cdn4.iconfinder.com/data/icons/leto-weather/64/na_not_available_weather_sun-512.png'" alt="Logo" class="rounded-full">
              </div>
            </th>
            <td data-label="Name" class="w-1/3">
              <router-link :to="`/projects/${asset.symbol}`"> {{ asset.name }}</router-link>
            </td>

            <td data-label="Marketcap">
              <small
            class="text-gray-500 dark:text-slate-400"
            >
              {{ formatValue(asset.marketcap, 'marketcap') }}
            </small>
            </td>

            <td class="before:hidden lg:w-1 whitespace-nowrap">
              <div class="h-6 w-6">       
                <a @click="isModalActive = true" class="clickable">          
                  <img src="https://cdn1.iconfinder.com/data/icons/shopping-and-ecommerce-2/64/E-commerce_and_Shopping-81-256.png" alt="Logo"  class="rounded-full">
                </a>       
                </div>
            </td>
          </tr>
        </tbody>
      </table>
      
      <!--PAGINATION-->
      <div class="p-3 lg:px-6 border-t border-gray-100 dark:border-slate-800">
        <BaseLevel>
          <BaseButtons>
            <BaseButton label="Previous" @click="currentPage = Math.max(currentPage - 1, 0)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
              <BaseButton
                v-for="page in pagesList.slice(Math.max(currentPage - 1, 0), Math.min(currentPage + 1, numPages))"
                :key="page"
                :active="page === currentPage"
                :label="page + 1"
                :color="page === currentPage ? 'lightDark' : 'whiteDark'"
                small
                @click="currentPage = page"
              />
              <BaseButton label="Next" @click="currentPage = Math.min(currentPage + 1, numPages - 1)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
        </BaseButtons>

          <small>Page {{ currentPageHuman }} of {{ numPages }}</small>
        </BaseLevel>
      </div>
</div>
</template>
<script>
import { mdiEye, mdiTrashCan } from "@mdi/js";
import CardBoxModal from "@/components/CardBoxModal.vue";
import TableCheckboxCell from "@/components/TableCheckboxCell.vue";
import BaseLevel from "@/components/BaseLevel.vue";
import BaseButtons from "@/components/BaseButtons.vue";
import BaseButton from "@/components/BaseButton.vue";
import UserAvatar from "@/components/UserAvatar.vue";
import { fetchSectorAssets, formatValue } from "@/stores/supastore";
import { supabase } from "../supabase";

export default {
  components: {
    CardBoxModal,
    TableCheckboxCell,
    BaseLevel,
    BaseButtons,
    BaseButton,
    UserAvatar,
    mdiEye,
    mdiTrashCan,
  },
  props: {
    sector: {
      type: String,
      default: 'File Storage'
    },
    pagesize: {
      type: Number,
      default: 5,
    }
  },
  data() {
    return {
      loading: true,
      assets: {},
      items: [],
      isModalActive: false,
      isModalDangerActive: false,
      perPage: this.pagesize,
      currentPage: 0,
      assetsPaginated: [],
    } 
  },
  async created() {
    this.assets = await fetchSectorAssets(this.sector);
    this.assets = this.assets['assets']
    this.updateAssetsPaginated();
  }, 
  watch: {
    currentPage() {
      this.assetsPaginated = Array.from(this.assets).slice(
        this.perPage * this.currentPage,
        this.perPage * (this.currentPage + 1)
      );
    }
  },
  computed: {
    numPages() {
      if (this.assets.length === 0) {
        return 0;
      }
      return Math.ceil(this.assets.length / this.perPage);
    },
    currentPageHuman() {
      return this.currentPage + 1;
    },
    pagesList() {
      const pagesList = [];
      for (let i = 0; i < this.numPages; i++) {
        pagesList.push(i);
      }
      return pagesList;
    },
  },
  methods: {
     formatValue(coin, metric) {
      return formatValue(coin, metric)
    },
    updateAssetsPaginated() {
    this.assetsPaginated = Array.from(this.assets).slice(
      this.perPage * this.currentPage,
      this.perPage * (this.currentPage + 1),
      this.loading = false
    );
  },
  }
}
</script>
